@charset "UTF-8";
html {
  font-size: 10px; }

body {
  background: #262161;
  background: #efefef; }
  body:before {
    content: '';
    display: block;
    height: 10px;
    width: 100%; }

.inner {
  max-width: 900px;
  margin: 0 auto; }
  @media all and (max-width: 900px) {
    .inner {
      padding: 2rem; } }

.nav {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  justify-content: space-between;
  background: #38507B; }
  .nav__section {
    display: flex; }
    .nav__section--search {
      flex: 1 1 auto; }
  .nav__item {
    display: flex; }
  .nav__link {
    background: #38507B;
    color: white;
    border-right: 1px solid rgba(255, 255, 255, 0.1);
    text-transform: uppercase;
    padding: 1.2rem 2rem 1rem 2rem;
    display: block;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: transform 0.2s;
    border-bottom: 5px solid transparent; }
    .nav__link svg {
      width: 40px;
      transition: all 0.2s;
      fill: white;
      margin-bottom: 1rem; }
    .nav__link:hover, .nav__link--active {
      border-bottom-color: rgba(0, 0, 0, 0.2);
      border-right-color: rgba(0, 0, 0, 0.05);
      background: #F2D796 85% fixed; }
      .nav__link:hover svg, .nav__link--active svg {
        transform: scale(1.2); }
    .nav__link--logo:hover svg {
      transform: none; }
    .nav__link--logo svg {
      width: 200px;
      margin: 0; }

@media all and (max-width: 1200px) {
  .nav__link {
    padding-left: 1rem;
    padding-right: 1rem; }
    .nav__link svg {
      width: 30px; }
    .nav__link--logo svg {
      width: 100px; } }

@media all and (max-width: 900px) {
  .nav {
    flex-wrap: wrap; }
    .nav__section {
      order: 1; }
      .nav__section--search {
        border-top: 1px solid rgba(255, 255, 255, 0.1);
        order: 2;
        width: 100%; } }

@media all and (max-width: 650px) {
  .nav__section {
    width: 100%; }
    .nav__section--user {
      order: -1;
      border-bottom: 1px solid rgba(255, 255, 255, 0.1); }
      .nav__section--user .avatar {
        min-width: 20px;
        height: 20px; }
      .nav__section--user svg {
        display: none; }
  .nav__item {
    width: 33.333333%; }
  .nav__link {
    width: 100%; }
    .nav__link svg {
      max-width: 100%; }
    .nav__link--logo svg {
      width: 100%; }
  .heart-count:before {
    content: '♥️';
    margin-right: 1rem; } }

.search {
  position: relative;
  width: 100%;
  display: flex; }
  .search__results {
    background: white;
    position: absolute;
    width: 100%;
    top: 100%;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    z-index: 5; }
  .search__input {
    font-size: 30px;
    background: none;
    color: white;
    outline: 0;
    border: 0; }
  .search__result {
    padding: 10px;
    display: block;
    border-bottom: 1px solid #ececec; }
    .search__result--active {
      background: #f1f1f1; }



